
#app {
  font-family: 'Arimo', sans-serif;
}
img.logo {
  max-height: 44px;
}
a.router-link-exact-active.exact {
  color: #fff !important;
}
a.router-link-active:not(.exact) {
  color: #fff !important;
}
a.disabled {
  pointer-events: none;
}
var {
  padding-right: 2px;
}
#alert_placeholder {
  bottom: 20px;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  z-index: 2;
  text-align: center;
  line-height: 2em;
}
#alertdiv {
  display: inline-table;
}
.hint {
  opacity: 0.4;
}
:hover > .hint {
  opacity: 1;
}
.container.main {
  margin-top: 4px;
}















































.blue a.router-link-active {
  color: #000 !important;
  border-color: #dee2e6 #dee2e6 #fff !important;
  background: -webkit-gradient(linear, left top, left bottom, from(#343a40), color-stop(5%, #343a40), color-stop(5%, white));
  background: linear-gradient(#343a40, #343a40 5%, white 5%);
}
.filter {
  margin: 1rem;
}
.middleText {
  margin: 50px auto;
  text-align: center;
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #ff0000; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  margin: 50px auto;
}
@-webkit-keyframes spin {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
@keyframes spin {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
ul.nav {
  height: 100%;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.contains-tabs {
  padding-left: 0;
  padding-right: 0;
}
.before-tabs {
  margin-left: 15px;
  padding-left: 0;
  border-bottom: 1px solid #dee2e6;
}
.after-tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #dee2e6;
}
.router-view {
  margin-top: 14px;
}
#profile {
  margin-top: 4px;
}

.progress-bar {
  background-color: red !important;
}
td.middle {
  vertical-align: middle;
  text-align: right;
}
.bar {
  width: 100%;
}































































